<template>
  <section class="add-address">
    <HeaderBar title="" :back="true"></HeaderBar>
    <TextInput labelTitle="" placeholder="" type="text" v-model="formate.receiverName" :maxlength="usrMaxlength"></TextInput>
    <TextInput labelTitle="" placeholder="" type="text" v-model="formate.mobile"  :maxlength="mobileMaxlength"></TextInput>
    <TextInput labelTitle="" placeholder="" type="text" arrow @hanlePicker="onShowArea" readonly :value="localRegion"></TextInput>
    <TextInput labelTitle="" placeholder="" type="area" v-model="formate.address" :maxlength="addresslength"></TextInput>
    <SetDefault @handleSet="onSet" :selected="formate.defaultStatus"></SetDefault>
    <section class="add-address-submit" @click="submitAddress"></section>

    <awesome-picker
      ref="picker"
      :textTitle="textTitle"
      :data="area"
      :anchor="anchor"
      @confirm="handlePickerConfirm">
    </awesome-picker>
  </section>
</template>
<script>
import { editExpressAddress, saveExpressAddress, getExpressAddressInfo } from '@/resource'
import HeaderBar from 'components/HeaderBar/index'

import area from '@/libs/area'
import TextInput from './components/TextInput'
import SetDefault from './components/SetDefault'
export default {
  name: 'addAddress',
  components: {
    TextInput,
    SetDefault,
    HeaderBar
  },
  data () {
    const _this = this
    const id = _this.$route.query.id ? _this.$route.query.id : ''
    console.log(area)
    return {
      usrMaxlength: 50,
      mobileMaxlength: 11,
      addresslength: 500,
      localAddress: null,
      textTitle: '',
      formate: {
        id: id,
        receiverName: '', // 
        mobile: '', // 
        province: '', // 
        city: '', // 
        district: '', // 
        address: '', // 
        defaultStatus: 1// true=
      },
      area: area,
      anchor: []
    }
  },
  watch: {
    localAddress: {
      handler (n) {
        const _this = this
        if (n) {
          const selectData = JSON.parse(n)
          _this.formate.province = selectData[0].value
          _this.formate.city = selectData[1].value
          _this.formate.district = selectData[2].value
        } else {
          _this.formate.province = ''
          _this.formate.city = ''
          _this.formate.district = ''
        }
      },
      deep: true
    }
  },
  computed: {
    localRegion () {
      const _this = this
      const onOff = !(_this.isInvalidString(_this.formate.province) || _this.isInvalidString(_this.formate.city) || _this.isInvalidString(_this.formate.district))
      if (onOff) {
        return `${_this.formate.province}-${_this.formate.city}-${_this.formate.district}`
      } else {
        return ''
      }
    }
  },
  methods: {
    isInvalidString (val) {
      /*eslint-disable*/
      if (((typeof val === 'string') && (val.length == 0)) || (val == undefined) || (val == 'undefined') || (val == 'null') || (typeof val === 'undefined') || (typeof val === 'null') || (val == null)) {
        return true
      } else {
        return false
      }
    },
    onSet (onOff) {
      const _this = this
      _this.formate.defaultStatus = onOff?1:0
      console.log(onOff ? '' : '')
    },
    onShowArea () {
      const _this = this
      _this.$refs.picker.show()
    },
    getLen(str) {
      // 21
      // 
      let realLength = 0; const len = str.length; let charCode = -1
      for (let i = 0; i < len; i++) {
        charCode = str.charCodeAt(i)
        if (charCode >= 0 && charCode <= 128) { realLength += 1 } else { realLength += 2 }
      }
      return realLength
    },
    checkForm (resolve,reject) {
      const _this = this
      const checkKeyName = {
        'receiverName':'~', // 
        'mobile':'~', // 
        'province':'~', // 
        'city':'~', // 
        'district':'~', // 
        'address':'~', // 
      }
      let completeOnOff = false
      let keyArr = Object.keys(checkKeyName)
      let errorMessage = ''
      for (let num = 0; num < keyArr.length; num++) {
        const keyName = keyArr[num]
        console.log(_this.formate[keyName],_this.isInvalidString(_this.formate[keyName]))
        completeOnOff = _this.isInvalidString(_this.formate[keyName])
        if (completeOnOff) {
          errorMessage = (checkKeyName[keyName])
          break
        }
      }
      if(!completeOnOff){
        if(_this.getLen(_this.formate.receiverName)>50){
          completeOnOff = true
          errorMessage = ''
        }
        if(!(/^1[23456789]\d{9}$/.test(_this.formate.mobile))){
          completeOnOff = true
          errorMessage = ''
        }
        if(_this.getLen(_this.formate.address)>500){
          completeOnOff = true
          errorMessage = ''
        }
      }
      return new Promise((resolve,reject)=>{
        if(!completeOnOff){
          resolve&&resolve()
        }else{
          reject&&reject(errorMessage)
        }
      })
    },
    submitAddress () {
      const _this = this
      _this.checkForm().then(async ()=>{
        let res = null
        if(_this.formate.id){
           res = await editExpressAddress(_this.formate)
        }else{
           res = await saveExpressAddress(_this.formate)
        }
        console.log(res)
        if(res){
          _this.Toast(_this.formate.id?'':'')
          debugger
          setTimeout(()=>{
            _this.$router.go(-1)
          },2000)
        }
      }).catch((errorMessage)=>{
        _this.Toast(_this.formate.id?'':'')
      })
    },
    handlePickerConfirm (value) {
      const _this = this
      _this.anchor = value
      console.log(value)
      _this.localAddress = value ? JSON.stringify(value) : null
    },
    async checkEditType(){
      const _this = this
      let appTitle = ''
      if(_this.formate.id){
        //
        appTitle = ''
        const res = await getExpressAddressInfo({id:_this.formate.id})
        Object.keys(_this.formate).forEach((key)=>{
          _this.formate[key] = res.rows[0][key]
        })
      }else{
        //
        appTitle = ''
      }
      document.title = appTitle
    }
  },
  mounted(){
    const _this = this
    document.title = ''
   _this.checkEditType()
  }
}
</script>
<style lang="scss" scoped>
  .add-address{
    height: 100vh;
    width: 100%;
    overflow: hidden;
    background: #fff;
    .add-address-submit{
      display: block;
      width: 600px;
      height: 90px;
      background:linear-gradient(90deg,rgba(51,218,138,1),rgba(18,201,138,1));
      border-radius: 45px;
      font-size: 39px;
      font-family: PingFang SC Medium, PingFang SC Medium-Medium;
      font-weight: 500;
      text-align: center;
      color: #ffffff;
      line-height: 90px;
      margin: 40px auto;
      letter-spacing: 0px;
      text-shadow: 0px 3px 5px 0px rgba(69,7,0,0.43);

    }
  }
</style>
